
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import mortgageList from './mortgageList'
import backList from './backList'
import AppCode from '@/components/common/Appcode'
import { guid } from '@/storage'
import { getMortgRecordlist, getMortgCode, getMortgReturnlist } from 'api'
export default {
data () {
return {
id: this.$route.params.id,
mortgId: this.$route.query.mortgid || '',
time: this.$route.query.time || '',
title: this.$route.query.title || '',
account: this.$route.query.account || '',
stickyTop: guid.get() ? 0 : 46,
info: '',
tabActive: 'mortgage',
list: [],
page: 1,
noData: true,
loading: false
}
},
computed: {},
watch: {
},
created () {
this.getList()
},
methods: {
changeItem (key) {
this.tabActive = key
if (key !== 'code') {
this.list = []
this.page = 1
this.noData = true
this.loading = false
this.getList()
}
if (key === 'code' && !this.info) {
this.getCode()
}
},
getList () {
if (this.loading) {
return
}
this.loading = true
if (this.tabActive === 'back') {
getMortgReturnlist({mortgId: this.mortgId, page: this.page})
.then(res => {
this.setList(res)
})
.finally(() => {
this.loading = false
})
}
if (this.tabActive === 'mortgage') {
getMortgRecordlist({id: this.id, page: this.page})
.then(res => {
this.setList(res)
})
.finally(() => {
this.loading = false
})
}
},
setList (res) {
let list = res.data
if (this.page <= 1) {
this.list = list
} else {
this.list.push(...list)
}
this.noData = (list.length < 10)
this.loading = false
},
loadMore () {
this.noData = true
this.page += 1
this.getList()
},
getCode () {
getMortgCode({mortgId: this.mortgId})
.then(res => {
this.info = res.data
})
}
},
components: {
XHeader,
Tab,
TabItem,
mortgageList,
backList,
Sticky,
AppCode,
LoadMore
}
}



