
import XDialog from 'vux/src/components/x-dialog/index.vue'

import { getMortgInfo } from 'api'
export default {
props: {
list: {
default: null
}
},
data () {
return {
showToast: false,
info: {},
statusObj: {
0: '<span style="color: #DF3B46">抵押中</span>',
1: '<span style="color: #43C062">返还中</span>',
2: '<span style="color: #666">返还结束</span>'
}
}
},
methods: {
setStatus (status) {
status = Number(status)
return this.statusObj[status]
},
getInfo (item) {
this.info = {}
getMortgInfo({ mortgId: item.mortgId, account: item.account })
.then(res => {
if (res.data && res.data.length) {
this.info = res.data[0]
}
this.info.account = item.account
this.showToast = true
})
}
},
components: {
XDialog
}
}



